import React from 'react';
import c from "./Users.module.css"
import {UsersPropsType} from "./UsersContainer";

const Users = (props: UsersPropsType) => {
    if (props.usersPage.users.length === 0) {
        props.setUsers(
            [
                {
                    id: 1,
                    photoUrl: "data:image/jpeg;base64,",
                    followed: false,
                    fullName: "Oleg",
                    status: "i bacame frontend-devoloper!",
                    location: {city: "Minsk", country: "Belarus"}
                },
                {
                    id: 2,
                    photoUrl: "data:image/jpeg;base64,",
                    followed: true,
                    fullName: "Diana",
                    status: "i am 'Jinka' Olega",
                    location: {city: "Minsk", country: "Belarus"}
                },
                {
                    id: 3,
                    photoUrl: "data:image/jpeg;base64,",
                    followed: false,
                    fullName: "Cavin",
                    status: "i want to sleep",
                    location: {city: "Toronto", country: "Canada"}
                },
                {
                    id: 4,
                    photoUrl: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQGZ437JiEsGq8Id1ZBtn2t0JV88bHevm2SNA&usqp=CAU",
                    followed: true,
                    fullName: "Sahra",
                    status: "great idea",
                    location: {city: "LA", country: "USA"}
                }
            ]
        )
    }

    return (
        <div>
            {
                props.usersPage.users.map(m => <div key={m.id}>
                    <span>
                        <div>
                            <img src={m.photoUrl} className={c.userPhoto} alt="photo"/>
                        </div>
                        <div>
                            {
                                m.followed
                                    ? <button onClick={() => props.unfollow(m.id)}>Unfollow</button>
                                    : <button onClick={() => props.follow(m.id)}>Follow</button>
                            }
                        </div>
                    </span>
                    <span>
                        <span>
                            <div>{m.fullName}</div>
                            <div>{m.status}</div>
                        </span>
                        <span>
                            <div>{m.location.country}</div>
                            <div>{m.location.city}</div>
                        </span>
                    </span>
                </div>)
            }
        </div>
    );
};

export default Users;